import React, { Component } from 'react';

import TodoList from './components/TodoList/TodoList';
import NewTodo from './components/NewTodo/NewTodo';
import Footer from './components/Footer/Footer';

class App extends Component {
  state = {
    todos: [],
    filter: 'all',
    onToggleAllDone: true,
  }

  addTodo = (todo) => {
    this.setState(prevState => ({
      todos: [...prevState.todos, todo],
    }));
  };

  onToggleDone = id => {
    this.setState(({ todos }) => ({
      todos: todos.map((todo) => {
        if (todo.id === id) {
          return { ...todo, completed: !todo.completed };
        }

        return todo;
      }),
    }));
  }

  onToggleAllDone = () => {
    this.setState(prevState => ({
      todos: prevState.todos.map(todo => ({
        ...todo,
        completed: prevState.onToggleAllDone,
      })),
      onToggleAllDone: !prevState.onToggleAllDone,
    }));
  }

  onDelete = (id) => {
    this.setState(prevState => ({
      todos: prevState.todos.filter(todo => todo.id !== id),
    }));
  }

  removeAllCompleteTodos = () => {
    this.setState(prevState => ({
      todos: prevState.todos.filter(todo => !todo.completed),
    }));
  }

  updateFilter = (string) => {
    this.setState({
      filter: string,
    });
  }

  render() {
    const { todos, filter } = this.state;

    let todosToShow = [];

    if (filter === 'all') {
      todosToShow = todos;
    } else if (filter === 'active') {
      todosToShow = todos.filter(todo => !todo.completed);
    } else if (filter === 'completed') {
      todosToShow = todos.filter(todo => todo.completed);
    }

    return (
      <section className="todoapp">
        <header className="header">
          <h1>todos</h1>
          <NewTodo addTodo={this.addTodo} />
        </header>

        <section className="main" style={{ display: 'block' }}>
          <input
            type="checkbox"
            id="toggle-all"
            className="toggle-all"
            onClick={this.onToggleAllDone}
          />
          <label htmlFor="toggle-all">Mark all as complete</label>

          <TodoList
            todos={todosToShow}
            onToggleDone={this.onToggleDone}
            onDelete={this.onDelete}
          />
        </section>

        <Footer
          todos={todos}
          removeAllCompleteTodos={this.removeAllCompleteTodos}
          filter={filter}
          updateFilter={this.updateFilter}
        />
      </section>
    );
  }
}

export default App;
